* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
}
.navbar {
  width: 100%;
  position: fixed;
  z-index: 1000;
}

.img {
  width: 80px;
  height: 80px;
  background-image: url('./Img/logo_vidanatura80.png');
  background-repeat: no-repeat;
  background-size: cover;
  margin-left: 20px;
  margin-right: 80px;
}

#menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  background-color: #20b2b2;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-around;
}

.li {
  display: inline-block;
  font-size: 1.5rem;
  flex-grow: 1;
}

.li_submenu {
  display: inline-block;
  font-size: 1.5rem;
  flex-grow: 1;
}

.li_dropdown {
  display: none;
  position: relative;
  min-width: 140px;
  font-size: 1.5rem;
  flex-grow: 1;
  z-index: 1;
}

.li_dropdown {
  transition-duration: 0.5s;
  color: #006497;
  display: block;
  position: absolute;
  flex-grow: 1;
}

.space {
  text-align: left;
  color: #ffffff;
  flex-grow: 2;
}

.link {
  position: relative;
  text-decoration: none;
  float: left;
  margin: 0;
  padding: 0;
  color: #ffffff;
}

.link:hover {
  transition-duration: 0.5s;
  color: #006497;
}

.dropdown {
  padding-top: 3px;
  font-size: 1.5rem;
  color: #ffffff;
  text-decoration: none
}

.menucomprimido {
  display: none;
}

#menu ul li {
  position: relative;
  float: left;
}

#menu ul ul {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  padding: 0;
}

#menu ul ul li {
  float: none;
  width: 250px;
}

#menu ul li:hover > ul {
  display: block;
}
